.swiper-box {
	position: relative;
}

// 與 .swiper-box 不同之處
// * Small + Medium 會貼齊邊緣
// * Large 顯示左右控制、Pagination
// * Large Hover: Hover 進範圍才會顯示
.swiper-box-global {
	position: relative;

	@include breakpoint(small only) {
		$padding-x: $padding-x-global-s;

		.swiper-container {
			margin-right: ($padding-x * -1);
			margin-left: ($padding-x * -1);
		}
	}

	@include breakpoint(medium only) {
		$padding-x: $padding-x-global-m;

		.swiper-container {
			margin-right: ($padding-x * -1);
			margin-left: ($padding-x * -1);
		}
	}

	@include breakpoint(large) {
		$padding-x: rem-calc(55);

		margin: rem-calc(-30) ($padding-x * -1);
		padding: rem-calc(30) $padding-x;
	}

	// * Large Hover: Hover 進範圍才會顯示
	@include breakpoint-hover {
		// Navigation
		.swiper-button-prev,
		.swiper-button-next {
			transition: opacity 0.3s, transform 0.3s;
		}

		.swiper-pagination {
			transition: opacity 0.3s;
		}

		&:not(:hover) {
			// Navigation
			.swiper-button-prev,
			.swiper-button-next {
				opacity: 0;
			}

			.swiper-button-next {
				transform: translateX(-10px);
			}

			.swiper-button-prev {
				transform: translateX(10px);
			}

			// Pagination
			.swiper-pagination {
				opacity: 0;
			}
		}
	}

	// Pagination
	.swiper-pagination {
		bottom: -22px;

		@include breakpoint(large) {
			bottom: -27px + 30;
		}
	}
}

// Navigation
%arrow-icon-style {
	width: 50px;
	height: 50px;
	font-size: rem-calc(18);
	color: $primary-color;

	&::before {
		@extend %font-icon-style;
	}

	&::after {
		display: none;
	}
}

.swiper-button-prev {
	@extend %arrow-icon-style;

	left: 0;

	&::before {
		@include font-icon-add(arrow-left, false);
	}
}

.swiper-button-next {
	@extend %arrow-icon-style;

	right: 0;

	&::before {
		@include font-icon-add(arrow-right, false);
	}
}

.swiper-button-disabled {
	opacity: 0 !important; // 為了蓋過 Hover 出現樣式設定，增加 !important
}

// Pagination
.swiper-pagination {
	left: 50%;
	transform: translateX(-50%);
	white-space: nowrap;
}

.swiper-pagination-bullet {
	margin-right: rem-calc(4);
	margin-left: rem-calc(4);
	width: 30px;
	height: 4px;
	border-radius: rem-calc(8);
	background: #eee;
	opacity: 1;
}

.swiper-pagination-bullet-active {
	background: $primary-color-lighten;
}

.swiper-pagination-bullets-dynamic {
	width: auto !important;

	.swiper-pagination-bullet {
		left: auto !important;
		margin: 0;
		width: 0;
		transition: 0.3s;
	}

	.swiper-pagination-bullet-active-next,
	.swiper-pagination-bullet-active-next-next,
	.swiper-pagination-bullet-active-prev,
	.swiper-pagination-bullet-active-prev-prev {
		margin: 0 rem-calc(4);
		width: 4px;
		transform: scale(1);
	}

	.swiper-pagination-bullet-active-main {
		margin: 0 rem-calc(4);
		width: 30px;
	}
}

