// Lazy
// 為了修正 IE11 不吃 variable Style
.swiper-lazy-preloader {
	border: 4px solid $themeColor;
	border-top-color: transparent;

	&-white {
		border-color: $white;
		border-top-color: transparent;
	}

	&-black {
		border-color: $black;
		border-top-color: transparent;
	}
}

