// Main Style
@import './layout/variable_mixin_function.scss';

// --------------------------------------------------------

/* Vendor */
@import 'vendor/swiper/swiper';

// --------------------------------------------------------

/* Component */

.pd-card {
	$root: &;

	@include breakpoint(large) {
		$gutter: rem-calc(10);
		$gutter-half: $gutter / 2;
		$gutter-half-minus: $gutter-half * -1;

		&.grid-x {
			margin-right: $gutter-half-minus;
			margin-left: $gutter-half-minus;

			> #{$root} {
				&__item {
					&.cell {
						margin-right: $gutter-half;
						margin-left: $gutter-half;
						width: calc(25% - #{$gutter});
					}
				}
			}
		}
	}

	@include breakpoint(xlarge) {
		$gutter: rem-calc(53);
		$gutter-half: $gutter / 2;
		$gutter-half-minus: $gutter-half * -1;

		&.grid-x {
			margin-right: $gutter-half-minus;
			margin-left: $gutter-half-minus;

			> #{$root} {
				&__item {
					&.cell {
						margin-right: $gutter-half;
						margin-left: $gutter-half;
						width: calc(25% - #{$gutter});
					}
				}
			}
		}
	}

	// SIT F153 - 統一首頁內頁手機版電腦版為8px
	// SIT F169 - 客戶要求調整首頁版的間距
	&__img-tag {
		@mixin tag-pos($bp: null, $pos-num: 6) {
			// $pos: rem-calc(6);
			$pos: rem-calc($pos-num);

			@at-root %pos-top-#{$bp} {
				top: $pos;
			}

			@at-root %pos-bottom-#{$bp} {
				bottom: $pos;
			}

			@at-root %pos-right-#{$bp} {
				right: $pos;
			}

			@at-root %pos-left-#{$bp} {
				left: $pos;
			}

			&.top-left {
				@extend %pos-top-#{$bp};
				@extend %pos-left-#{$bp};
			}

			&.top-right {
				@extend %pos-top-#{$bp};
				@extend %pos-right-#{$bp};
			}

			&.bottom-left {
				@extend %pos-bottom-#{$bp};
				@extend %pos-left-#{$bp};
			}

			&.bottom-right {
				@extend %pos-bottom-#{$bp};
				@extend %pos-right-#{$bp};
			}
		}

		@mixin tag-pos-bp($bp: null, $pos-num: 6) {
			@if $bp == null {
				@include tag-pos($bp, $pos-num);
			} @else {
				@include breakpoint($bp) {
					@include tag-pos($bp, $pos-num);
				}
			}
		}

		@include tag-pos-bp(medium, 12);
	}

	&__title {
		&.h5 {
			@include breakpoint(medium) {
				font-size: rem-calc(18);
			}
		}
	}

	&__price {
		@include breakpoint(medium) {
			font-size: rem-calc(20); // 還原 h5 大小
		}
	}
}

// SIT F169: 客戶要求客製化調整首頁各標籤愛心大小
@include breakpoint(medium) {
	// Fav
	.pd-card__btn-fav {
		margin-right: rem-calc(-7 + 5);
		margin-top: rem-calc(-8 + 4);
		font-size: rem-calc(36);
	}

	// 3D
	.tag-3d--circle {
		width: rem-calc(34);
		height: rem-calc(34);
		font-size: rem-calc(17);

		@at-root .chrome.win & {
			&::before {
				transform: translateY(1px);
			}
		}

		@at-root .chrome.mac & {
			&::before {
				transform: translateY(0.5px);
			}
		}

		@at-root .safari & {
			&::before {
				transform: translate(1px, -0.5px);
			}
		}
	}

	// Tag
	.pd-card__img-tag-group.bottom-left {
		bottom: rem-calc(16);
	}

	.tag-gray,
	.tag-green {
		font-size: rem-calc(14);
	}
}

// --------------------------------------------------------

/* Container */
$index-hd-height: 125; // Header Height
$index-banner-adjust: 20; // Banner 區域往上調整多少空間

.index-key-vision {
	$root: &;

	position: relative;

	@include breakpoint(large) {
		// (1/2 為了做滑鼠偵測的區域範圍的設置)
		padding-top: rem-calc($index-hd-height - $index-banner-adjust); // 125: Header Height
	}

	@at-root %index-banner-bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		pointer-events: none;
	}

	// 背景圖片還沒Load進來的假背景
	// 為了要可以淡出，所以設置在::before
	&::before {
		@extend %index-banner-bg;

		content: '';
		z-index: -1;
		background-image: linear-gradient(
			to bottom,
			#005379 -3%,
			#1dc9a3 48%,
			rgba(38, 208, 126, 0.2) 76%,
			rgba(38, 208, 124, 0)
		);
		transition: opacity 0.3s;
	}

	&.loaded-content {
		&::before {
			opacity: 0;
		}
	}

	//- ↓放背景圖
	&__bg {
		@extend %index-banner-bg;

		background: repeat-x left top;
		background-size: auto 100%;
		opacity: 0;
		transition: opacity 0.3s;

		@include breakpoint(large) {
			height: calc(100% + #{(275px - 130px)});
			background-position-x: -210px;
		}

		&.loaded {
			$el: &;

			opacity: 1;
			background-image: url('../images/index/banner_bg_m--uc.png');

			@include breakpoint(large) {
				background-image: url('../images/index/banner_bg--uc.png');
			}
		}
	}

	//- ↓放Slider相關的Bg
	&__deco {
		@extend %index-banner-bg;

		background-size: cover;
		// -webkit-mask-image: url('../images/index/banner_bg_m_mask--uc.png');
		// mask-image: url('../images/index/banner_bg_m_mask--uc.png');
		-webkit-mask-size: auto 100%;
		mask-size: auto 100%;
		-webkit-mask-repeat: no-repeat;
		mask-repeat: no-repeat;

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: no-repeat center center / cover;
		}

		@at-root #{$root}__bg.loaded {
			~ #{$root}__deco {
				-webkit-mask-image: url('../images/index/banner_bg_m_mask--uc.png');
				mask-image: url('../images/index/banner_bg_m_mask--uc.png');

				&::before {
					background-image: url('../images/index/banner-bg-deco@3x--uc.png');
				}
			}
		}

		@include breakpoint(large) {
			display: none;
		}
	}
}

.index-key-circle {
	$root: &;

	@include selection-none;

	position: relative;
	padding-top: rem-calc(56 + 18);
	color: $white;

	@include breakpoint(large) {
		// (2/2 為了做滑鼠偵測的區域範圍的設置)
		padding-top: rem-calc(230 - $index-hd-height + $index-banner-adjust); // 125: Header Height
	}

	&__wrapper {
		position: relative;

		@include breakpoint(medium down) {
			padding-bottom: rem-calc(10);
		}
	}

	&__cont {
		position: relative;
		z-index: 5;
		display: flex;
	}

	&__text-box {
		flex: 1;
	}

	&__title {
		font-weight: $font-bold-b;

		@include breakpoint(large) {
			margin-bottom: rem-calc(8 + 5);
		}
	}

	// 輪播物件 ======
	&__item {
		position: relative;
		width: percentage(127 / 375) / 1% * 1vw; // 把單位換成 vw
	}

	// <a>
	&__link {
		width: 100%;
		display: block;

		&:focus {
			outline: 0 none;
		}
	}

	&__img-box {
		// 背景圖
		&--back {
			position: relative;
			z-index: 5;
			display: block;
			background-position: center center;
			background-size: cover;
			border: 1px solid transparent;
			border-radius: 999px;
			box-shadow: 0 0 10px 0 rgba($black, 0.04);

			// 稱高度的物件，不做其它樣式
			&::before {
				content: '';
				display: block;
				padding-top: 100%;
			}
		}

		// 前景圖（破圖設計）
		&--front {
			$size: #{percentage(570 / 381)};

			position: absolute;
			z-index: 10;
			top: 50%;
			left: 50%;
			display: block;
			width: $size;
			height: $size;
			transform: translate(-50%, -50%);
			pointer-events: none;
		}
	}

	// =============================
	// 圓形裝飾：為了比較好辨識，把全部都拆成有命名的物件，不是before after
	// 因為要符合 IE，所以不能使用 Variable CSS
	$border-hl-color: #31fcff; // Active 更改強調 Border 顏色用

	@mixin circle-box-absolute(
		// 0的話一定要單位，與有圖的中心距離多少
		$space: 0px,
		// 0的話一定要單位
		$border-width: 0px,
		// 是否生成所有的
		$build-all: false
	) {
		@if $build-all {
			content: '';
			position: absolute;
			border-radius: 999px;
		}

		top: calc(#{$space} * -1 - #{$border-width});
		left: calc(#{$space} * -1 - #{$border-width});
		width: calc(100% + #{$space} * 2);
		height: calc(100% + #{$space} * 2);
	}

	@at-root %circle-box-absolute {
		@include circle-box-absolute(0px, 0px, true);
		// --space: 0px; // 0的話一定要單位，與有圖的中心距離多少
		// --border-width: 0px; // 0的話一定要單位

		// content: '';
		// position: absolute;
		// top: calc(var(--space) * -1 - var(--border-width));
		// left: calc(var(--space) * -1 - var(--border-width));
		// width: calc(100% + var(--space) * 2);
		// height: calc(100% + var(--space) * 2);
		// border-radius: 999px;
	}

	// 白色+透明度形成的圓
	@at-root %circle-box-wave {
		@extend %circle-box-absolute;

		// --space: 0px;

		z-index: 1;
		background: $white;
		opacity: 0.15;
		box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.04);
		pointer-events: none;
	}

	// 圖外層會看到的發光物件(Active)
	&__hl-border {
		@extend %circle-box-absolute;

		$space: 0px;
		$border-width: 2px;

		@include circle-box-absolute($space, $border-width);

		border: solid $border-width $border-hl-color;
		opacity: 0;

		@include breakpoint(medium) {
			$space: 0px;
			$border-width: 3px;

			@include circle-box-absolute($space, $border-width);
		}
	}

	// 白色外圈同大小有模糊會發光的圓（Active）
	&__hl-blur {
		@extend %circle-box-absolute;

		$space: rem-calc(5);
		$border-width: 2px;
		$shadow-spread: 10px;

		@include circle-box-absolute($space, $border-width);

		border: solid $border-width $border-hl-color;
		opacity: 0;
		filter: blur(2px);
		box-shadow: 0 0 $shadow-spread 0 rgba(0, 0, 0, 0.04);

		@include breakpoint(medium) {
			$space: rem-calc(5);
			$border-width: 3px;

			@include circle-box-absolute($space, $border-width);

			border-width: $border-width;
		}
	}

	// 白色外圈：會一直看到的
	@mixin circle-light-border($border-width: 1px, $space: rem-calc(5)) {
		@include circle-box-absolute($space, $border-width);

		border: $border-width solid $white;
	}

	&__light-border {
		@extend %circle-box-absolute;

		@include circle-light-border;
	}

	// 白背 + 透明度(第一層)
	$circle-fill-space: rem-calc((164 - 128) / 2);

	@at-root %circle-fill {
		opacity: 0;

		&::before {
			@extend %circle-box-wave;

			$space: $circle-fill-space;
			$border-width: 0px;

			@include circle-box-absolute($space, $border-width);

			transition: transform 0.3s;
		}
	}

	&__circle-fill-1 {
		@extend %circle-fill;

		opacity: 1;
	}

	// 白背 + 透明度(第二層)：會一直看到
	&__circle-fill-2 {
		@extend %circle-fill;
	}

	// 白背 + 透明度(最外層)：用來做 Active 顯示
	&__circle-fill-3 {
		@extend %circle-fill;
	}

	// 白背 + 透明度：用來做 Active Wave
	&__circle-wave {
		opacity: 0;

		&::before {
			@extend %circle-box-wave;
		}

		&::after {
			@extend %circle-box-wave;
		}
	}

	// Active
	.swiper-slide-active,
	.is-active {
		#{$root} {
			&__img-box {
				&--back {
					border-color: $border-hl-color;
				}
			}
			// =============================
			// 圓形裝飾：為了比較好辨識，把全部都拆成有命名的物件，不是before after
			// 圖外層會看到的發光物件(Active)
			&__hl-border {
				opacity: 1;
				animation: circle-light 2s infinite;
			}

			// 白色外圈同大小有模糊會發光的圓（Active）
			&__hl-blur {
				$space: rem-calc(10);
				$border-width: 2px;

				@include circle-box-absolute($space, $border-width);

				opacity: 1;
				animation: circle-light-shadow 2s infinite;

				@include breakpoint(medium) {
					$space: rem-calc(10);
					$border-width: 3px;

					@include circle-box-absolute($space, $border-width);
				}
			}

			// 白色外圈：會一直看到的
			&__light-border {
				$space: rem-calc(10);
				$border-width: 1px;

				@include circle-box-absolute($space, $border-width);

				@include breakpoint(medium) {
					$space: rem-calc(10);
					$border-width: 2px;

					@include circle-box-absolute($space, $border-width);

					border: $border-width solid $white;
				}
			}

			// 白背 + 透明度(第一層)
			&__circle-fill-1 {
				&::before {
					$space: rem-calc(10);
					$border-width: 0px;

					@include circle-box-absolute($space, $border-width);
				}
			}

			// 白背 + 透明度(第二層)：會一直看到
			&__circle-fill-2 {
				opacity: 1;

				&::before {
					transform: scale(#{(170 / 164)});
					transition-delay: 0.3s;
				}
			}

			// 白背 + 透明度(最外層)：用來做 Active 顯示
			&__circle-fill-3 {
				opacity: 1;

				&::before {
					transform: scale(#{(189 / 164)});
					transition-delay: 0.5s;
				}
			}

			&__circle-wave {
				opacity: 1;

				&::before,
				&::after {
					animation: circle-wave 3s infinite;
				}

				&::after {
					animation-delay: 0.5s;
				}
			}

			@keyframes circle-light {
				0%,
				10%,
				90%,
				100% {
					opacity: 1;
				}

				50% {
					opacity: 0.2;
				}
			}

			@keyframes circle-light-shadow {
				0%,
				10%,
				90%,
				100% {
					$shadow-spread: 10px;

					box-shadow: 0 0 $shadow-spread 0 rgba(0, 0, 0, 0.04);
					opacity: 1;
					transform: scale(1);
				}

				50% {
					$shadow-spread: 20px;

					box-shadow: 0 0 $shadow-spread 0 rgba(0, 0, 0, 0.04);
					opacity: 0.2;
					transform: scale(1.05);
				}
			}

			@keyframes circle-wave {
				0% {
					opacity: 0;
					transform: scale(1);
				}

				20% {
					opacity: 0.15;
					transform: scale(1);
				}

				100% {
					opacity: 0;
					// transform: scale(#{(189 / 127)});
					transform: scale(2);
				}
			}
		}
	}

	@include breakpoint(medium down) {
		&__text-box {
			margin-top: rem-calc(-3);
			margin-bottom: rem-calc(0);
			padding-right: rem-calc(80);
		}

		.swiper-container {
			$padding-y: rem-calc(40);

			overflow: visible;
			margin-right: ($padding-y * -1);
			margin-left: ($padding-y * -1);
			padding-top: $padding-y;
			padding-bottom: $padding-y;
			opacity: 0;
		}

		.swiper-container-initialized {
			opacity: 1;
			transition: opacity 1s;
		}

		.swiper-wrapper {
			transform: translateX(#{(202 / 375 * 100vw)}) !important;
		}
	}

	@include breakpoint(medium only) {
		&__text-box {
			margin-bottom: rem-calc(30);
		}
	}

	@include breakpoint(large) {
		&__cont {
			pointer-events: none;
		}

		&__text-box {
			margin-left: percentage(342 / 1120); // 左側距離（與內容寬邊緣）/ 內容寬
			margin-bottom: percentage(237 / 1120); // 左側距離（與內容寬邊緣）/ 內容寬
			max-width: rem-calc(320);
		}

		&__btn-box {
			display: none;
		}

		&__item {
			// 遵守以下事項，避免影響 swiper 取得的數值
			// - 不設定 position(absolute, top, left) 相關設定
			// - 不設定 margin (margin-top: height / -2, margin-left: width / -2)
			// - 只設定寬高
			width: 215px;
			height: 215px;
		}

		&__link {
			position: relative;
		}

		@at-root %swiper-wrapper-absolute {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
		}

		.swiper-box {
			@extend %swiper-wrapper-absolute;

			left: 100px;
			width: calc(100% - 100px * 2);

			@include breakpoint(xlarge) {
				left: 160px;
				width: calc(100% - 160px * 2);
			}
		}

		.swiper-container {
			@extend %swiper-wrapper-absolute;

			overflow: visible;
		}

		.swiper-wrapper {
			@extend %swiper-wrapper-absolute;

			$width: 143%;

			width: $width;
			height: auto;
			top: auto;
			bottom: 26%;
			left: ($width - 100%) / 2 * -1;
			border-radius: 50%;

			@include breakpoint(1440) {
				$width: 130%;

				width: $width;
				left: ($width - 100%) / 2 * -1;
			}

			@include breakpoint(1600) {
				$width: percentage(1440 / 1120);

				bottom: 47%;
				width: $width;
				left: ($width - 100%) / 2 * -1;
			}

			&::before {
				content: '';
				display: block;
				width: 100%;
				padding-top: 100%;
			}
		}

		.swiper-slide {
			&:not(.swiper-slide-active) {
				a,
				button {
					pointer-events: none;
				}
			}

			&.swiper-slide-active,
			&.swiper-slide-duplicate-active {
				// 第二項
				+ .swiper-slide {
					z-index: 4;

					#{$root} {
						&__light-border {
							$border-width: 4px; // 調整成觀看起來類似視覺稿的粗細

							@include circle-light-border($border-width);
						}
					}
				}

				// 第三項
				+ .swiper-slide + .swiper-slide {
					z-index: 3;
					pointer-events: none;

					#{$root} {
						&__light-border {
							$border-width: 4px; // 調整成觀看起來類似視覺稿的粗細

							@include circle-light-border($border-width);
						}
					}
				}

				// 第四項
				+ .swiper-slide + .swiper-slide + .swiper-slide {
					z-index: 2;
					pointer-events: none;

					#{$root} {
						&__light-border {
							$border-width: 3px; // 調整成觀看起來類似視覺稿的粗細

							@include circle-light-border($border-width);
						}
					}
				}
			}

			// Safari 在 transform 300ms 的時候會閃爍，會很消耗效能，但只能出此下策
			@at-root .fixed-will-change .swiper-slide {
				* {
					will-change: transform;
				}
			}
		}
	}

	// 特殊尺寸，客戶希望窄版螢幕可以看到下方廣告Banner
	@include breakpoint-between(large, 1600 - 1) {
		// (2/2 為了做滑鼠偵測的區域範圍的設置)
		padding-top: rem-calc(180 - $index-hd-height + $index-banner-adjust); // 125: Header Height
		padding-bottom: rem-calc(50); // 125: Header Height

		&__text-box {
			margin-bottom: 10%;
		}
	}

	// 滑鼠互動
	@include breakpoint-hover {
		&.mouse-init {
			$size: 48;
			$size-half: $size / 2;

			@at-root %index-banner-cursor-left {
				cursor: url('../images/mouse-arrow-left.png') $size $size, auto;
			}

			@at-root %index-banner-cursor-right {
				cursor: url('../images/mouse-arrow-right.png') $size $size, auto;
			}

			&[data-area='left'] {
				@extend %index-banner-cursor-left;
			}

			&[data-area='right'] {
				@extend %index-banner-cursor-right;

				a {
					@extend %index-banner-cursor-right;
				}
			}
		}
	}
}

