// 廣播(跑馬燈)
.index-alert {
	position: relative;
	z-index: 1;
	padding-top: rem-calc(20);

	@include breakpoint(small only) {
		padding-bottom: rem-calc(8);
	}

	@include breakpoint(medium) {
		margin-bottom: rem-calc(-17);
	}

	&__cont {
		position: relative;
		display: flex;
		align-items: center;
		margin-right: auto;
		margin-left: auto;
		padding-right: rem-calc(60);
		max-width: rem-calc(750 + 60); // 60 = 插圖
		min-height: rem-calc(40);
	}

	&__list {
		margin-bottom: 0;
		white-space: nowrap;
		display: flex;
	}

	// User Name + 物品名稱
	&__item {
		position: relative;
		padding: 0 rem-calc(20);
		font-size: rem-calc(14);
		font-weight: $font-bold;
		white-space: nowrap;
		color: $primary-dark;

		@include breakpoint(medium) {
			font-size: rem-calc(16);
		}

		// 物品名稱（強調的項目，橘色）
		em {
			font-style: normal;
			color: $primary-orange-dark;
		}

		&::after {
			content: '|';
			position: absolute;
			top: 0;
			right: 0;
		}
	}

	&__img-box {
		position: absolute;
		top: 50%;
		right: 0;
		width: 73px;
		height: 40px;
		pointer-events: none;
		background: no-repeat center center / cover;
		transform: translateY(-50%);

		&.loaded {
			background-image: url('../images/brand_ip/alert.svg');
		}
	}
}

.marquee {
	overflow: hidden;
}

// --------------------------------------------------------

// 活動 Banner
.index-banner {
	$root: &;

	@include breakpoint(small only) {
		padding-bottom: rem-calc(20 + 9);
	}

	&__list {
		list-style: none;
		margin: 0;
	}

	@at-root %border-radius {
		border-radius: rem-calc(12);
	}

	&__item {
		@extend %border-radius;

		@include breakpoint(medium down) {
			width: (287 / 375 * 100vw);
			max-width: 433px;
		}

		@at-root .swiper-container:not(.swiper-container-initialized) & {
			@include breakpoint(large) {
				width: calc((100vw - #{rem-calc(100 * 2 + 26 * 1)}) / 2);
				margin-right: rem-calc(26);
			}

			@include breakpoint(1200) {
				width: calc((100vw - #{rem-calc(100 * 2 + 26 * 2)}) / 3);
				margin-right: rem-calc(26);
			}

			@include breakpoint(1120 + 200) {
				width: calc((#{rem-calc(1120px)} - #{rem-calc(26 * 2)}) / 3);
			}
		}
	}

	&__link {
		display: block;
	}

	&__img-box {
		@extend %border-radius;

		// 拯救Safari圓角矩形無法運作的Bug
		// 為了修正Safari Hover 後，"圖片 + border-radius + overflow hidden"
		// 修正1: https://stackoverflow.com/a/35251979/11240898
		position: relative;
		z-index: 1;

		// 修正2: https://stackoverflow.com/a/16681137/11240898
		-webkit-backface-visibility: hidden;
		-moz-backface-visibility: hidden;
		-webkit-transform: translate3d(0, 0, 0);
		-moz-transform: translate3d(0, 0, 0);

		overflow: hidden;
		background: $loading-bg;
		box-shadow: global-box-shadow(20px, 0.34);

		&::before {
			@include before-img-size(600, 210, true);
		}

		&::after {
			@extend %border-radius;
		}
	}

	// Swiper - Navigation
	.swiper-button-prev,
	.swiper-button-next {
		@include breakpoint(medium down) {
			display: none;
		}
	}

	@include breakpoint-hover {
		&__img-box {
			transition: opacity 0.3s;
		}

		&__img {
			// Fixed Image Scale blurry
			// https://stackoverflow.com/a/36787557/11240898
			-webkit-backface-visibility: hidden;
			-ms-transform: translateZ(0); /* IE 9 */
			-webkit-transform: translateZ(0); /* Chrome, Safari, Opera */
			transform: translateZ(0) scale(1.01);
			transition: transform 0.3s;
		}

		&__link {
			&:hover,
			&:focus {
				#{$root} {
					&__img {
						transform: scale(1.1);
					}
				}
			}
		}

		.swiper-slide-prev,
		.swiper-slide-next {
			&__img-box {
				opacity: 0.8;
			}

			&:hover,
			&:focus {
				#{$root} {
					&__img-box {
						opacity: 0.8;
					}
				}
			}
		}
	}
}

// --------------------------------------------------------

// 官方旗艦店
.index-store {
	$root: &;
	$col: 3.5; // 出現幾個
	$gutter: 10;
	$gutter-half: $gutter / 2;
	$padding-x: $padding-x-global-s;

	.grid-container {
		@include breakpoint(medium down) {
			overflow: hidden; // 為了隱藏滾軸(1/3)
		}
	}

	&__list-wrapper {
		@include breakpoint(large) {
			$expand: rem-calc(10); // 向外擴展多少 (為了要顯示 Hover Shadow 的空間)

			margin: ($expand * -1); // 為了要顯示 Hover Shadow 的空間(1/2)
			padding: $expand; // 為了要顯示 Hover Shadow 的空間(1/2)

			@at-root %shadow-mask {
				content: '';
				position: absolute;
				z-index: 5;
				top: 0;
				width: $expand;
				height: 100%;
			}

			&::before {
				@extend %shadow-mask;

				left: 0;
				background: linear-gradient(to right, $white 0%, rgba($white, 0));
			}

			&::after {
				@extend %shadow-mask;

				right: 0;
				background: linear-gradient(to left, $white 0%, rgba($white, 0));
			}
		}
	}

	// 【可滑動區域】<ul>(.swiper-container)
	&__list {
		margin: 0;
		list-style: none;

		@include breakpoint(medium down) {
			$padding-x: $padding-x-global-s;

			display: flex;
			overflow-x: auto;
			width: auto;
			margin: 0 rem-calc($gutter-half * -1);
			margin-bottom: rem-calc(-10); // 為了隱藏滾軸(2/3)
			padding: 0 $padding-x;
			padding-bottom: rem-calc(10); // 為了隱藏滾軸(3/3)
			white-space: nowrap;
			-webkit-overflow-scrolling: touch;
		}

		@include breakpoint(medium only) {
			$padding-x: $padding-x-global-m;

			padding-right: $padding-x;
			padding-left: $padding-x;
		}
	}

	// <li>
	&__item {
		@include breakpoint(small only) {
			$padding-x: $padding-x-global-s;
			$width: calc((100vw - #{$padding-x}) / #{$col} - #{rem-calc($gutter)});

			// flex: 0 0 #{$width};
			width: #{$width};
			margin-left: rem-calc($gutter-half);
			margin-right: rem-calc($gutter-half);
		}

		@include breakpoint(medium only) {
			$col: 4.5; // 出現幾個
			$gutter: 20;
			$gutter-half: $gutter / 2;
			$padding-x: $padding-x-global-m;

			$width: calc((100vw - #{$padding-x}) / #{$col} - #{rem-calc($gutter)});

			// flex: 0 0 #{$width};
			width: #{$width};
			margin-left: rem-calc($gutter-half);
			margin-right: rem-calc($gutter-half);
		}

		@include breakpoint(large) {
			$col: 6;
			$width: 120px;

			width: $width;

			&:not(:last-child) {
				margin-right: calc((100vw - 200px - #{$width} * #{$col}) / #{$col - 1});
			}

			@include breakpoint(xlarge) {
				$col: 6;

				&:not(:last-child) {
					margin-right: calc((100vw - 200px - #{$width} * #{$col}) / #{$col - 1});
				}
			}

			@include breakpoint(1320) {
				// $col: 7;

				&:not(:last-child) {
					// margin-right: calc((1320px - #{$width} * #{$col}) / #{$col - 1});
					margin-right: 45px;
				}
			}
		}
	}

	&__link {
		display: block;
		text-align: center;
		color: $primary-dark;

		// 蓋過 Foundation 樣式
		&:hover,
		&:focus {
			color: $primary-dark;
		}
	}

	// 文字區
	&__text-box {
		@include clamp-line-height(2, 1.2);

		margin-bottom: 0;
		white-space: normal;
	}

	&__img-box {
		@include border-gradient; // 漸層 Border

		overflow: hidden;
		margin-bottom: rem-calc(10);
		border-radius: rem-calc(20);
		box-shadow: 0 2px 4px 0 rgba(221, 233, 242, 0.7);

		// 拯救Safari圓角矩形無法運作的Bug
		// 因為圖片有使用transform之類的樣式，在外層增加這些樣式
		-webkit-backface-visibility: hidden;
		-moz-backface-visibility: hidden;
		-webkit-transform: translate3d(0, 0, 0);
		-moz-transform: translate3d(0, 0, 0);
	}

	@include breakpoint-hover {
		&__img {
			transition: transform 0.3s;
		}

		&__link {
			&:hover,
			&:focus {
				#{$root} {
					&__img {
						transform: scale(1.1);
					}
				}
			}
		}
	}

	// Swiper

	.swiper-container {
		// Swiper 尚未生成 (避免造成頁面大量偏移(CLS))
		&:not(.swiper-container-initialized) {
			#{$root} {
				&__item {
					@include breakpoint(large) {
						$col: 6;
						$gutter: 20px;
						// $max: 100vw - 100px * 2;

						margin-right: $gutter;
						width: calc(((100vw - 100px * 2) - #{$gutter} * (#{$col} - 1)) / #{$col});
					}

					// 1320
					@include breakpoint(1120 + 100 * 2) {
						$col: 6;
						$gutter: 45px;
						$max: 1120px;

						margin-right: $gutter;
						width: calc((#{$max} - #{$gutter} * (#{$col} - 1)) / #{$col});
					}

					// 1440
					@include breakpoint(xxlarge) {
						$col: 7;
						$gutter: 45px;
						$max: 1120px;

						margin-right: $gutter;
						width: calc((#{$max} - #{$gutter} * (#{$col} - 1)) / #{$col});
					}
				}
			}
		}
	}

	// Nav
	.swiper-button-prev,
	.swiper-button-next {
		// 因為會有兩行的狀況，所以要用實數
		top: rem-calc(120 / 2 + 30); // 30: 區塊 padding-top

		@include breakpoint(medium down) {
			display: none;
		}
	}
}

