// 首頁商品列表
.index-pd-list {
	// 【APP版本】篩選按鈕 => 隱藏
	&__btn-filter {
		display: none;
	}
}

// --------------------------------------------------------

// 【Web版本】沒有找到想要的寶物
.index-pd-more {
	position: relative;
	z-index: 1;
	padding-bottom: rem-calc(80);

	@include breakpoint(small only) {
		padding-top: rem-calc(40);
	}

	@include breakpoint(large) {
		padding-bottom: rem-calc(124);
	}

	.sec-title-box {
		margin-bottom: rem-calc(14 + 7);

		@include breakpoint(medium) {
			margin-bottom: rem-calc(34 - 9);
		}

		&__title {
			.text {
				// 插圖設定
				$img-origin-width: 80.9619; // 原圖大小
				$img-origin-height: 56.64; // 原圖大小

				// 圖片呈現尺寸 => 使用原圖大小直接計算高
				$img-width: 59px;

				position: relative;
				padding-right: rem-calc($img-width + 10);

				// 標題旁邊的裝飾 IP
				&::after {
					content: '';
					position: absolute;
					top: 50%;
					right: 0;
					display: inline-block;
					margin-top: -2px;
					width: $img-width;
					height: ($img-width * $img-origin-height / $img-origin-width);
					// background-image: url('../images/index/brand-ip-question.svg');
					background-repeat: no-repeat;
					background-size: contain;
					transform: translate(0, -50%);
				}

				@include breakpoint(medium) {
					$img-width: 82px;

					padding-right: rem-calc($img-width + 10);

					&::after {
						margin-top: -2px; // 往上移
						margin-right: -3px; // 往上移
						width: $img-width;
						height: ($img-width * $img-origin-height / $img-origin-width);
					}
				}
			}

			&.loaded {
				.text {
					&::after {
						background-image: url('../images/brand_ip/question.svg');
					}
				}
			}
		}
	}

	&__desc {
		margin-bottom: rem-calc(29);
		line-height: 1.6;

		@include breakpoint(medium) {
			margin-bottom: rem-calc(48);
		}
	}

	.btn {
		margin-top: rem-calc(4);
	}
}

