		border-radius: $radius / 5 * 3;
		box-shadow: 0 2px 20px rgba($black, 0.1);

		&::before {
			@include before-img-size(280, 374);
		}
	}

	&__checkbox-box {
		margin-bottom: rem-calc(7);
	}

	.close-button {
		top: 0;
		right: 0;
		width: rem-calc(40);
		height: rem-calc(42);
		background: $white;
		border-bottom-left-radius: $radius;
		font-size: rem-calc(14);

		&::before {
